import React from 'react'
import HomePage from './HomePage'
import MainPage from './MainPage'
import {BrowserRouter as Router,Switch,Route} from 'react-router-dom'

export default function App() {
  return (
    <>
    <Router>
      <Switch>
        <Route exact path="/">
          <HomePage />
        </Route>
        <Route path="/main">
          <MainPage />
        </Route>
      </Switch>
      </Router>
    </>
  );
}
