import React from 'react'
import './mainpage-hegiht.css'
import Nav from './Nav'
import SearchForm from './SearchForm'
import BgShape from './bgShape'
import InsideNav from './InsideNav'
import Button from '@material-ui/core/Button';
import CardContainer from './CardContainer'
export default function MainPage() {
    return (
        <div>
           <BgShape/>
           <Nav/>
            <div className="container-fluid">
               <div className="row mt-5">
                <div className="col-lg-3 col-sm-2"></div> 
                <div className="col-lg-6 col-sm-8">
                    <SearchForm/>
                </div> 
                <div className="col-lg-3 col-sm-2"></div>
            </div>
            <p className="p  text-center text-secondary m-0">או</p>
            <div className="bg-light text-dark py-0 my-2 w-25 mx-auto rounded">
                <p className="p  text-center text-secondary m-0">חיפוש לפי אות</p>
            </div>
            <div className="container mb-5">
                <div className="row mx-auto">
                    <div className="row mx-auto">
                    {['א','ב','ג','ד','ה','ו','ז','ח','ט','י','כ','ל','מ','נ','ס','ע','פ','צ','ק','ר','ש','ת',]
                    .map(item => {
                        return <div key={item} className="col p-1 mx-auto justify-content-center text-light text-center">
                            <Button variant="outlined" className="text-light btn-lg btn-block ed">{item}</Button>
                        </div>
                    })}
                    </div>
                </div>
            </div>
           </div>
           <div className="container-fluid bg-light mx-auto">
                <div className="row m-1  d-flex">
                    <div className="col col-sm-12 justify-content-center">
                        <CardContainer/>
                        <CardContainer/>
                        <CardContainer/>
                        <CardContainer/>
                    </div>
                </div>
           </div>
           <div className="d-flex justify-content-center fixed-bottom btn-click" >
                <InsideNav />
            </div>
        </div>
    )
}
